import {
    actionModal
} from "./modal";


function post(trigger, modal, modalThanks, overlay, menu, container, body) {
    $(trigger).submit(function (e) {
        e.preventDefault();
        $.ajax({
            type: "POST",
            url: "mailer/smart.php",
            data: $(this).serialize()
        }).done(function () {
            $(this).find("input").val("");
            actionModal('close', modal, overlay, menu, container, body);
            actionModal('open', modalThanks, overlay, menu, container, body);

            setTimeout(() => {
                actionModal('close', modalThanks, overlay, menu, container, body);
            }, 5000);

            $(trigger).trigger('reset');
        });
        return false;
    });
}

export default post;