function cursesBlock(btn, lineOne, lineTwo, activeStyle, activeContent, activeLine1, activeLine2) {
    const btns = document.querySelectorAll(btn);

    btns.forEach(btn => {
        btn.addEventListener('click', function () {
            // hideContent();
            const line1 = this.querySelector(lineOne);
            const line2 = this.querySelector(lineTwo);
            this.classList.toggle(activeStyle);
            this.nextElementSibling.classList.toggle(activeContent);
            line1.classList.toggle(activeLine1);
            line2.classList.toggle(activeLine2);

            if (this.classList.contains(activeStyle)) {
                this.nextElementSibling.style.maxHeight = '450px';
            } else {
                this.nextElementSibling.style.maxHeight = '0px';
            }
        });
    });

    // function hideContent() {
    //     btns.forEach(btn => {
    //         btn.classList.remove('active-style');
    //         btn.nextElementSibling.classList.remove('active-content');
    //         btn.nextElementSibling.style.maxHeight = '0px';
    //     });
    // }
}
export default cursesBlock;