import {
    Swiper
} from "../services/swiper-bundle.min"

function swiperBlock(trigger, pagination) {
    const swiper = new Swiper(trigger, {
        spaceBetween: 40,
        slidesPerView: 3,
        centeredSlides: true,
        roundLengths: true,
        loop: true,
        loopAdditionalSlides: 30,
        autoplay: {
            delay: 5000,
            pauseOnMouseEnter: true,
            disableOnInteraction: false,
        },

        pagination: {
            el: pagination,
            clickable: true,
        },
    })
}

export default swiperBlock;